.updates {
  padding: 20px 50px 40px 50px;
  margin: 0 auto;
}

.popular {
  padding: 2rem;
  display: flex;
  justify-content: center;
}

.popular_anime {
  width: 19rem !important;
  transition: transform 0.3s ease, filter 0.3s ease;
  z-index: 2;
  margin-left: 1rem;
}

.popular_anime:hover {
  transform: scale(1.1);
}

.popular_anime img {
  width: 19rem;
  transform: translate(0, 0%);
}

.popular_anime:hover img {
  filter: blur(3px) brightness(0.3);
}

.popular_anime_item__text {
  position: fixed;
  display: none;
  z-index: 1;
  padding: 25px 0 0 10px;
  width: 18rem;
}

.popular_anime:hover .popular_anime_item__text {
  display: inline-block;
  z-index: 3;
}
