.schedule_day {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    color: #ffffff;
    padding: 2rem;
}

.anime {
    width: 19rem;
    transition: transform 0.3s ease, filter 0.3s ease;
    z-index: 2;
}

.anime:hover {
    transform: scale(1.1);
}

.anime img {
    width: 19rem;
    transform: translate(0, 0%);
}

.anime:hover img {
    filter: blur(3px) brightness(0.3);
}

.anime_item__text {
    position: fixed;
    display: none;
    z-index: 1;
    padding: 10px 0 0 10px;
}

.anime:hover .anime_item__text {
    display: inline-block;
    z-index: 3;
}
