.anilist_page {
    background: #dddddd;
    padding: 20px 0 20px 0;
    justify-content: center;
    color: #ffffff;
}

a {
    text-decoration: none;
    color: inherit;
}

.anime_list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 75px;
    justify-content: center;
    gap: 2rem;
}

.anime {
    width: 19rem;
    transition: transform 0.3s ease, filter 0.3s ease;
    z-index: 2;
}

.anime:hover {
    transform: scale(1.1);
}

.anime img {
    width: 19rem;
    transform: translate(0, 0%);
}

.anime:hover img {
    filter: blur(3px) brightness(0.3);
}

.anime_item__text {
    position: fixed;
    display: none;
    z-index: 1;
    padding: 10px 0 0 10px;
    width: 18rem;
}

.anime:hover .anime_item__text {
    display: inline-block;
    z-index: 3;
}

.loading {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
}